import React from 'react';
import '../index.scss';
import './Location.scss';

export default class Location extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            locations: [
                "Praha",
                "Brno",
                "Ostrava",
                "Plzeň",
                "Liberec",
                "Olomouc",
                "České Budějovice",
                "Ústí nad Labem",
                "Hradec Králové",
                "Pardubice",
                "Zlín",
                "Karlovy Vary",
                "Tábor"
            ]
        }
    }
    render() {
        const gridItems = this.state.locations.map((item, index) => {
            return (
                <div className="gridItem" key={index}>
                    <div className="radioContainer">
                        <input type="radio" id={item} name="location" value={item} onChange={this.props.handleInputChange} />
                        <label htmlFor={item} className="radioLabel">
                            {item}
                            <span className="checkMark"></span>
                        </label>
                        
                    </div>
                </div>
            )
        });
        return (
            <div className="Location container">
                <h1>Kde se nachází vaše nemovitost?</h1>
                <p>Vyberte město</p>
                <form>
                    <div className="gridContainer">
                        {gridItems}
                    </div>
                    <button className="nextBtn" onClick={() => {this.props.handleClick("property")}}>Pokračovat</button>
                </form>
            </div>
        );
    }
}