import React from 'react';
import '../index.scss';
import './Contacts.scss';

export default class Contacts extends React.Component {
    render() {
        return (
            <div className="Contacts container">
                <h1>Zadejte vaše kontaktní údaje</h1>
                <p>Představte se</p>
                <form className="contactsForm">
                    <label className="contactsLabel">
                        Jméno a příjmení
                        <input className="contactsInput" type="text" name="name" onChange={this.props.handleInputChange} required />
                    </label>
                    <label className="contactsLabel">
                        E-mail
                        <input className="contactsInput" type="email" name="email" onChange={this.props.handleInputChange} required />
                    </label>
                    <label className="contactsLabel">
                        Telefon
                        <input className="contactsInput" type="tel" name="tel" onChange={this.props.handleInputChange} required />
                    </label>
                    <label className="contactsCheckboxLabel">
                        Souhlasím se zpracováním osobních údajů pro obchodní účely
                        <input className="contactsCheckbox" type="checkbox" required />
                        <span className="checkMark"></span>
                    </label>
                    <button className="nextBtn" onClick={this.props.makePost}>Odeslat</button>
                </form>
            </div>
        );
    }
}